.about-container {
  background-color: rgba(21, 37, 53, 0.009);
  padding: 2rem;
  border-radius: 50px;
  min-height: 80vh;
  margin-block: 2rem;

  &.night {
    color: rgb(238, 237, 237);

    .big-header {
        color: rgb(238, 237, 237);
    }
  }

  .about-header {
    margin-block-end: 2rem;
  }

  .about-body {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    font-size: 1.5rem;

    @media (max-width: 450px) {
      font-size: 1.1rem;
    }

    @media (max-width: 1100px) {
      flex-direction: column;
    }

    .about-info {
      max-width: 500px;
      line-height: 35px;
      font-family: 'Nanum Gothic';
    }

    .about-img {
      height: 300px;
      width: 300px;
      border-radius: 50%;
      background: url('https://gcdnb.pbrd.co/images/bWgLVYWD8SYr.png?o=1');
      background-size: contain;
      transition: all 0.4s ease;
      background-repeat: no-repeat;
      background-position: center;
      @media (max-width: 420px) {
        height: 250px;
        width: 250px;
      }
    }
  }

  .contact {
    margin-block-start: 5rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    font-size: 1.5rem;
    div {
      gap: 20px;
    }
  }
}

.contact-ways {
  img {
    height: 50px;
  }

  .gmail-img {
    margin-top: 5px;
  }
}
